@font-face {
  font-family: "Raleway";
  font-style: normal;
  font-weight: 400;
  src: url("../../src/fonts/Raleway/Raleway-Regular.ttf") format("truetype");
}
@font-face {
  font-family: "Raleway";
  font-style: normal;
  font-weight: 700;
  src: url("../../src/fonts/Raleway/Raleway-Bold.ttf") format("truetype");
}
@font-face {
  font-family: "Raleway";
  font-style: normal;
  font-weight: 900;
  src: url("../../src/fonts/Raleway/Raleway-Black.ttf") format("truetype");
}
@font-face {
  font-family: "Staatliches";
  font-style: normal;
  font-weight: 400;
  src: url("../../src/fonts/Staatliches/Staatliches-Regular.ttf") format("truetype");
}
[class*=__container] {
  width: min(95%, 120rem);
  margin-inline: auto;
}

*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  font-size: 62.5%;
  scroll-behavior: smooth;
}

body {
  min-width: 32rem;
  font: 400 1.4rem/1.5 "Raleway", sans-serif;
}
@media (min-width: 768px) {
  body {
    font: 400 1.6rem/1.5 "Raleway", sans-serif;
  }
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
}

h1 {
  font: 400 3.2rem/1.2 "Staatliches", sans-serif;
}

h2 {
  font: 400 2.4rem/1.2 "Staatliches", sans-serif;
}

h3 {
  font: 400 1.6rem/1.2 "Staatliches", sans-serif;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

ul {
  list-style: none;
}

input,
textarea {
  outline: none;
}

.header {
  padding-block: 2.4rem;
}
@media (min-width: 768px) {
  .header {
    padding-block: 3.2rem;
  }
}
.header__logo {
  width: 25rem;
  margin-inline: auto;
}
@media (min-width: 768px) {
  .header__logo {
    width: 30rem;
  }
}
.header__flex {
  display: flex;
  flex-direction: column;
}
@media (min-width: 768px) {
  .header__flex {
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
  }
}
.header__menu {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 0.8rem;
}
@media (min-width: 768px) {
  .header__menu {
    flex-direction: row;
    gap: 1.6rem;
  }
}
.header__link {
  font-weight: 700;
  color: #000;
}
.header__link:nth-child(1) {
  color: #B8336A;
}

.hero {
  background-image: url("../img/hero.jpg");
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  padding-block: 5em;
}
@supports (background-image: url("../img/hero.webp")) {
  .hero {
    background-image: url("../img/hero.webp");
  }
}
@supports (background-image: url("../img/hero.avif")) {
  .hero {
    background-image: url("../img/hero.avif");
  }
}
@media (min-width: 768px) {
  .hero {
    padding-block: 10em;
  }
}
.hero__grid {
  display: grid;
  place-items: center;
}
.hero__content {
  width: 90%;
  max-width: 50rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 2.4rem;
}
.hero__titulo {
  text-align: center;
  color: #FFFFFF;
}
@media (min-width: 768px) {
  .hero__titulo {
    font-size: 4rem;
  }
}
@media (min-width: 1024px) {
  .hero__titulo {
    font-size: 4.8rem;
  }
}
.hero__boton {
  width: 100%;
  padding: 0.8rem 3.2rem;
  border: none;
  display: inline-block;
  text-align: center;
  font-weight: 700;
  color: #FFFFFF;
  background-color: #ff8015;
  cursor: pointer;
}
@media (min-width: 768px) {
  .hero__boton {
    width: auto;
  }
  .hero__boton:hover {
    background-color: #e16700;
  }
}

.buscar, .categorias, .eventos {
  padding-block: 2.4rem;
}
@media (min-width: 768px) {
  .buscar, .categorias, .eventos {
    padding-block: 3.2rem;
  }
}
.buscar__titulo, .categorias__titulo, .eventos__titulo {
  margin-bottom: 2.4rem;
  text-align: center;
  color: #484349;
}
@media (min-width: 768px) {
  .buscar__titulo, .categorias__titulo, .eventos__titulo {
    font-size: 3.2rem;
  }
}
.buscar__formulario, .categorias__grid, .eventos__grid {
  display: grid;
  gap: 1.6rem;
}
@media (min-width: 768px) {
  .buscar__formulario, .categorias__grid, .eventos__grid {
    grid-template-columns: repeat(4, 1fr);
  }
}
.buscar__label {
  overflow: hidden;
  width: 1px;
  height: 1px;
  display: block;
}
.buscar__select, .buscar__input {
  width: 100%;
  height: 100%;
  padding: 0.8rem;
  border: 1px solid #484349;
}
.buscar__boton {
  width: 100%;
  padding: 0.8rem 3.2rem;
  border: none;
  display: inline-block;
  text-align: center;
  font-weight: 700;
  color: #FFFFFF;
  background-color: #B8336A;
  cursor: pointer;
}
@media (min-width: 768px) {
  .buscar__boton {
    width: auto;
  }
  .buscar__boton:hover {
    background-color: #902853;
  }
}
@media (min-width: 768px) {
  .buscar__boton {
    width: 100%;
    height: 100%;
  }
}

@media (min-width: 768px) {
  .eventos__grid {
    grid-template-columns: repeat(3, 1fr);
  }
}
.eventos__evento {
  border: 1px solid #e1e1e1;
}
.eventos__img {
  width: 100%;
}
.eventos__content {
  padding: 1.6rem;
  display: flex;
  flex-direction: column;
  gap: 0.8rem;
}
.eventos__meta {
  font-size: 1.2rem;
}
.eventos__nombre {
  font-family: "Raleway", sans-serif;
  font-weight: 900;
  color: #484349;
}
.eventos__user {
  display: flex;
  align-items: center;
  gap: 2.4rem;
}
.eventos__img-user {
  width: 6rem;
  border-radius: 50%;
}
.eventos__organizado {
  margin-bottom: 0.2rem;
  font-weight: 700;
}
@media (min-width: 768px) {
  .eventos__organizado {
    font-size: 1.4rem;
  }
}
.eventos__nombre-user {
  font-size: 1.2rem;
}

@media (min-width: 768px) {
  .categorias__grid {
    grid-template-columns: repeat(3, 1fr);
  }
}
@media (min-width: 1024px) {
  .categorias__grid {
    grid-template-columns: repeat(4, 1fr);
  }
}
.categorias__img {
  width: 100%;
}
.categorias__nombre {
  margin-top: 0.8rem;
  text-align: center;
  font-weight: 700;
}

.grupos {
  padding-block: 5rem;
  background-image: linear-gradient(to bottom, #B8336A 0%, #B8336A 50%, #068D9D 50%, #068D9D 100%);
}
@media (min-width: 768px) {
  .grupos {
    padding-block: 10rem;
    background-image: linear-gradient(to right, #B8336A 0%, #B8336A 50%, #068D9D 50%, #068D9D 100%);
  }
}
.grupos__grid {
  display: grid;
  grid-template-rows: 1fr;
  gap: 4rem;
  place-items: center;
}
@media (min-width: 768px) {
  .grupos__grid {
    grid-template-rows: unset;
    grid-template-columns: repeat(2, 1fr);
  }
}
.grupos__grupo {
  max-width: 40rem;
}
.grupos__titulo, .grupos__text {
  color: #FFFFFF;
}
.grupos__titulo {
  margin-bottom: 1.6rem;
}
@media (min-width: 768px) {
  .grupos__titulo {
    font-size: 3.2rem;
  }
}
@media (min-width: 1024px) {
  .grupos__titulo {
    font-size: 4rem;
  }
}
@media (min-width: 768px) {
  .grupos__text {
    font-size: 1.8rem;
  }
}
@media (min-width: 1024px) {
  .grupos__text {
    font-size: 2rem;
  }
}

.footer {
  padding: 4rem 0 2rem;
}
@media (min-width: 768px) {
  .footer {
    padding: 6rem 0 3rem;
  }
}
@media (min-width: 1024px) {
  .footer {
    padding: 8rem 0 4rem;
  }
}
.footer__copy {
  margin-top: 2.4rem;
  text-align: center;
}
@media (min-width: 768px) {
  .footer__copy {
    margin-top: 3.2rem;
  }
}
@media (min-width: 1024px) {
  .footer__copy {
    margin-top: 4.2rem;
  }
}